import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DiscountCustomerComponent } from './discount-customer.component';
import { RouterModule, Routes } from '@angular/router'; 
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component: DiscountCustomerComponent
  },
  {
    path: 'sales-discount',
    loadChildren: () => import('./sales-discount/sales-discount.module').then(m => m.SalesDiscountModule)
  },
  {
    path: 'invoice-discount',
    loadChildren: () => import('./invoice-discount/invoice-discount.module').then(m => m.InvoiceDiscountModule)
  }  
]


@NgModule({
  declarations: [
    DiscountCustomerComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class DiscountModule { }
