import { VhComponent } from './../../../../components/vh-component/vh-component';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-discount',
  templateUrl: './discount-customer.component.html',
  styleUrls: ['./discount-customer.component.scss']
})
export class DiscountCustomerComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private lang: LanguageService

  ) { }

  ngOnInit(): void {
  }

  gotoSalesDiscount() {
    if (this.vhAuth.checkMyPermission('management_enable_sales_discount'))
      this.router.navigate(['/sales/dashboard/manage/sales-program/discount-customer/sales-discount'])
    else this.vhComponent.alertMessageDesktop("error", this.lang.translate("You do not have this rights"), 3000)
  }
  gotoInvoiceDiscount() {
    if (this.vhAuth.checkMyPermission('management_enable_sales_invoice_discount'))
      this.router.navigate(['/sales/dashboard/manage/sales-program/discount-customer/invoice-discount'])

    else this.vhComponent.alertMessageDesktop("error", this.lang.translate("You do not have this rights"), 3000)
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program'])
  }

}
